.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
          box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}
.modal-body {
  margin: 20px 0;
}
.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */
.modal-enter {
  opacity: 0;
}
.modal-leave-active {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}












































































































































































































































































































































































































































































































































































































































































































#container {overflow:hidden;height:600px;position:relative;
}
#mapWrapper {width:100%;height:600px;z-index:1;
}
#rvWrapper {width:50%;height:600px;top:0;right:0;position:absolute;z-index:0;
}
#container.view_roadview #mapWrapper {width: 50%;
}
#roadviewControl {position:absolute;top:5px;left:280px;width:42px;height:42px;z-index: 1;cursor: pointer; background: url(http://t1.daumcdn.net/localimg/localimages/07/2018/pc/common/img_search.png) 0 -450px no-repeat;
}
#roadviewControl.active {background-position:0 -350px;
}
#close {position: absolute;padding: 4px;top: 5px;left: 5px;cursor: pointer;background: #fff;border-radius: 4px;border: 1px solid #c8c8c8;-webkit-box-shadow: 0px 1px #888;box-shadow: 0px 1px #888;
}
#close .img {display: block;background: url(http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/rv_close.png) no-repeat;width: 14px;height: 14px;
}
.map_wrap, .map_wrap * {margin:0;padding:0;font-family:'Malgun Gothic',dotum,'\B3CB\C6C0',sans-serif;font-size:12px;
}
.map_wrap a, .map_wrap a:hover, .map_wrap a:active{color:#000;text-decoration: none;
}
.map_wrap {position:relative;width:100%;height:600px;
}
#menu_wrap {position:absolute;top:0;left:0;bottom:0;width:250px;margin:10px 0 30px 10px;padding:5px;overflow-y:auto;background:rgba(255, 255, 255, 0.7);z-index: 1;font-size:12px;border-radius: 10px;
}
.bg_white {background:#fff;
}
#menu_wrap hr {display: block; height: 1px;border: 0; border-top: 2px solid #5F5F5F;margin:3px 0;
}
#menu_wrap .option{text-align: center;
}
#menu_wrap .option p {margin:10px 0;
}
#menu_wrap .option button {margin-left:5px;
}
#placesList li {list-style: none;
}
#placesList .item {position:relative;border-bottom:1px solid #888;overflow: hidden;cursor: pointer;min-height: 65px;
}
#placesList .item span {display: block;margin-top:4px;
}
#placesList .item h5, #placesList .item .info {text-overflow: ellipsis;overflow: hidden;white-space: nowrap;
}
#placesList .item .info{padding:10px 0 10px 45px;
}
#placesList .info .gray {color:#8a8a8a;
}
#placesList .info .jibun {padding-left:26px;background:url(http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_jibun.png) no-repeat;
}
#placesList .info .tel {color:#009900;
}
#placesList .item .markerbg {float:left;position:absolute;width:36px; height:37px;margin:10px 0 0 10px;background:url(http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png) no-repeat;
}
#placesList .item .marker_1 {background-position: 0 -10px;
}
#placesList .item .marker_2 {background-position: 0 -56px;
}
#placesList .item .marker_3 {background-position: 0 -102px
}
#placesList .item .marker_4 {background-position: 0 -148px;
}
#placesList .item .marker_5 {background-position: 0 -194px;
}
#placesList .item .marker_6 {background-position: 0 -240px;
}
#placesList .item .marker_7 {background-position: 0 -286px;
}
#placesList .item .marker_8 {background-position: 0 -332px;
}
#placesList .item .marker_9 {background-position: 0 -378px;
}
#placesList .item .marker_10 {background-position: 0 -423px;
}
#placesList .item .marker_11 {background-position: 0 -470px;
}
#placesList .item .marker_12 {background-position: 0 -516px;
}
#placesList .item .marker_13 {background-position: 0 -562px;
}
#placesList .item .marker_14 {background-position: 0 -608px;
}
#placesList .item .marker_15 {background-position: 0 -654px;
}
#pagination {margin:10px auto;text-align: center;
}
#pagination a {display:inline-block;margin-right:10px;
}
#pagination .on {font-weight: bold; cursor: default;color:#777;
}
























